.metamask-modal{
    display: flex !important;
    align-items: center;
    justify-content: center;
  }
  .metamask-modal .modal-content {
    padding: 40px 32px 24px;
    box-shadow: 0px 3px 5px -1px rgb(0 0 0 / 20%), 0px 5px 8px 0px rgb(0 0 0 / 14%), 0px 1px 14px 0px rgb(0 0 0 / 12%);
    border-radius: 30px;
    background-color: #fff;
    text-align: center;
    filter:drop-shadow(1px 1px 2px  #fff);
  }
  .paper:focus {
    outline: none;
  }

  .metamaskBtn {
    width: 80%;
    height: 60px;
    margin: 20px 0;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    border-radius: 10px;
    background-image: linear-gradient(267.54deg, rgb(255, 220, 36) 1.63%, rgb(255, 92, 0) 98.05%);
    border: none;

  }
  .metamaskBtn:hover {
    transform: translateY(-2px);
    box-shadow: 0px 8px 15px rgb(0 0 0 / 25%);
 }

  .metamaskBtnContent {
    color: #fff;
    width: 100%;
    margin: 0px;
    display: flex;
    padding: 0px 24px;
    align-items: center;
    font-weight: 600;
    letter-spacing: 1px;
    justify-content: space-between;
  }
  .retryBtn {
    height: 60px;
    background: #FDC644;
    border: 1px solid #FDC644;
    border-radius: 20px;
    width: 60%;
    margin: 20px 0;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
  }
  .retryBtn:hover{
    background: #000;
    color: #fff;
    box-shadow: 0px 8px 15px rgb(0 0 0 / 25%);
    transform: translateY(-2px);
  }

  .linkStyle {
    display: block;
    padding-top: 6px;
    text-decoration: none;
    color: #000;
  }
  .linkStyle:hover{
    color: #FDC644;

  }